<template>
    <div>
        <v-list-item :class="{ 'light-blue lighten-4': tarefa.concluido }"
            @click="tarefa.concluido = !tarefa.concluido">
            <template v-slot:default="{}">
                <v-list-item-action>
                    <v-checkbox :input-value="tarefa.concluido"></v-checkbox>
                </v-list-item-action>

                <v-list-item-content>
                    <v-list-item-title :class="{ 'text-decoration-line-through': tarefa.concluido }">{{
                        tarefa.titulo
                    }}</v-list-item-title>
                </v-list-item-content>

                <v-btn icon @click.stop="handleRemoveTarefa(tarefa.id)">
                    <v-icon color="red darken-1">mdi-trash-can</v-icon>
                </v-btn>
            </template>
        </v-list-item>
        <v-divider></v-divider>
    </div>
</template>

<script>
export default {
    props: ['tarefa'],
    methods: {
        handleRemoveTarefa(id) {
            this.$store.commit('removeTarefa', id)
        }
    }
}
</script>

